import React, { createContext, useState, useContext, useEffect } from 'react';

// Cration du contexte d'authentification
const AuthContext = createContext(null);

// Provider qui enveloppera l'application
export const AuthProvider = ({ children }) => {
  const [currentUser, setCurrentUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [userProfile, setUserProfile] = useState(null);

  useEffect(() => {
    // Simuler la rcupration du user depuis le stockage local ou l'API
    const checkUserAuth = async () => {
      try {
        const storedUser = localStorage.getItem('user');
        if (storedUser) {
          const user = JSON.parse(storedUser);
          setCurrentUser(user);
          
          // Rcuprer le profil utilisateur
          await fetchUserProfile(user.id);
        }
      } catch (error) {
        console.error("Erreur lors de la vrification de l'authentification:", error);
      } finally {
        setLoading(false);
      }
    };

    checkUserAuth();
  }, []);

  // Rcuprer le profil utilisateur depuis l'API
  const fetchUserProfile = async (userId) => {
    try {
      // Pour la demo, crons un profil simul
      const defaultProfile = {
        id: userId,
        firstName: "Utilisateur",
        lastName: "Demo",
        level: "intermdiaire",
        ftp: 250,
        weight: 75,
        height: 180,
        goals: ["amlioration FTP", "grimper cols"],
        preferences: {
          theme: "standard",
          notifications: true,
          units: "metric"
        },
        trainingStats: {
          totalDistance: 2500,
          totalElevation: 35000,
          totalRides: 45,
          totalTime: 120
        }
      };
      
      setUserProfile(defaultProfile);
      return defaultProfile;
    } catch (error) {
      console.error("Erreur lors de la rcupration du profil utilisateur:", error);
      return null;
    }
  };

  // Fonction de connexion
  const login = async (email, password) => {
    try {
      // En dmo, on utilise un utilisateur fictif
      const demoUser = {
        id: "user_123456",
        email: email,
        name: "Cycliste Demo",
        authToken: "jwt_token_demo_123456789"
      };

      setCurrentUser(demoUser);
      localStorage.setItem('user', JSON.stringify(demoUser));
      
      // Rcuprer le profil utilisateur
      await fetchUserProfile(demoUser.id);
      
      return { success: true, user: demoUser };
    } catch (error) {
      console.error("Erreur lors de la connexion:", error);
      return { success: false, error: error.message };
    }
  };

  // Fonction de dconnexion
  const logout = () => {
    setCurrentUser(null);
    setUserProfile(null);
    localStorage.removeItem('user');
  };

  // Enregistrement d'un nouvel utilisateur
  const register = async (email, password, name) => {
    try {
      // En dmo, on utilise un utilisateur fictif
      const newUser = {
        id: "user_" + Date.now(),
        email,
        name,
        authToken: "jwt_token_new_" + Date.now()
      };

      setCurrentUser(newUser);
      localStorage.setItem('user', JSON.stringify(newUser));
      
      // Crer un profil utilisateur par dfaut
      const defaultProfile = {
        id: newUser.id,
        firstName: name.split(' ')[0],
        lastName: name.split(' ').slice(1).join(' '),
        level: "dbutant",
        ftp: 200,
        weight: 75,
        height: 175,
        goals: ["amlioration endurance"],
        preferences: {
          theme: "standard",
          notifications: true,
          units: "metric"
        }
      };
      
      setUserProfile(defaultProfile);
      return { success: true, user: newUser };
    } catch (error) {
      console.error("Erreur lors de l'enregistrement:", error);
      return { success: false, error: error.message };
    }
  };

  // Mise  jour du profil utilisateur
  const updateUserProfile = async (profileData) => {
    try {
      const updatedProfile = { ...userProfile, ...profileData };
      setUserProfile(updatedProfile);
      
      // Normalement on ferait un appel API ici
      console.log("Profil utilisateur mis  jour:", updatedProfile);
      
      return { success: true, profile: updatedProfile };
    } catch (error) {
      console.error("Erreur lors de la mise  jour du profil:", error);
      return { success: false, error: error.message };
    }
  };

  // Valeurs exposes par le contexte
  const value = {
    currentUser,
    userProfile,
    loading,
    login,
    logout,
    register,
    updateUserProfile,
    fetchUserProfile
  };

  return (
    <AuthContext.Provider value={value}>
      {!loading && children}
    </AuthContext.Provider>
  );
};

// Hook personnalis pour utiliser le contexte d'authentification
export const useAuth = () => {
  return useContext(AuthContext);
};

export default AuthContext;
